@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  body {
    @apply bg-[#0a0a1a] text-white overflow-x-hidden;
  }
}

@layer components {
  .btn {
    @apply px-6 py-3 rounded-lg font-medium transition-all duration-300 relative overflow-hidden text-base md:text-lg;
  }
  
  .btn-primary {
    @apply bg-gradient-to-r from-blue-500 to-purple-600 hover:from-blue-600 hover:to-purple-700 text-white shadow-lg shadow-blue-500/20;
  }

  .container {
    @apply max-w-7xl mx-auto px-2 sm:px-4 md:px-6 lg:px-8;
  }

  .glass-card {
    @apply bg-white/5 backdrop-blur-lg border border-white/10 rounded-xl w-full md:w-auto;
  }

  .gradient-text {
    @apply bg-clip-text text-transparent bg-gradient-to-r from-blue-400 to-purple-500;
  }

  .space-gradient {
    @apply bg-gradient-to-b from-[#0a0a1a] via-[#1a1a3a] to-[#0a0a1a];
  }
}

/* Мобильная адаптивность */
@media (max-width: 768px) {
  h1, .text-5xl { font-size: 2rem !important; }
  h2, .text-4xl { font-size: 1.5rem !important; }
  .glass-card { padding: 1.25rem !important; }
  .btn { font-size: 1rem !important; padding: 0.75rem 1.25rem !important; }
  .container { padding-left: 0.5rem !important; padding-right: 0.5rem !important; }
}

/* Анимации */
@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

.animate-float {
  animation: float 6s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.5; }
}

.animate-pulse-slow {
  animation: pulse 3s ease-in-out infinite;
}

@keyframes fade-in {
  0% { opacity: 0; transform: translateY(20px); }
  100% { opacity: 1; transform: translateY(0); }
}

.animate-fade-in {
  animation: fade-in 0.8s cubic-bezier(0.4,0,0.2,1) both;
}

/* Скроллбар */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  @apply bg-[#0a0a1a];
}

::-webkit-scrollbar-thumb {
  @apply bg-gradient-to-b from-blue-500 to-purple-600 rounded-full;
} 